.toggle-button {
  width: 30px;
  height: 22px;
  cursor: pointer;
  z-index: 1001;
  background: none;
  border: none;
  position: relative;
}

.toggle-button:before, .toggle-button:after, .toggle-button__line {
  content: "";
  width: 100%;
  height: 3px;
  background-color: #000;
  transition: all .3s;
  position: absolute;
  left: 0;
}

.toggle-button:before {
  top: 0;
}

.toggle-button__line {
  top: 50%;
  transform: translateY(-50%);
}

.toggle-button:after {
  bottom: 0;
}

.toggle-button.open:before {
  top: 50%;
  transform: rotate(45deg);
}

.toggle-button.open:after {
  top: 50%;
  bottom: auto;
  transform: rotate(-45deg);
}

.toggle-button.open .toggle-button__line {
  opacity: 0;
}

@media (min-width: 768px) {
  .toggle-button {
    display: none;
